@import '../style/variables.scss';
@import '../style/button.scss';

.content-wrapper{

    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: 1fr 1fr 12fr 2fr 3fr 1fr;    

    width: 90vw;
    height: 90vh;

    margin: 5vh 5vw;
    padding: 3vh 5vw;
    box-sizing: border-box;
    border: solid $green3 8px;
    border-radius: 1px;

    .menudisplay-wrapper {
        display: flex;
        flex-direction: column;
        justify-content: center;
    }

    .title-wrapper {
        font-size: 20px;
        display: flex;
        // height: 30px;
        height: 1px;
        .title {
            margin: 0 auto;
            position: relative;
            top: -2vh;
        }
    
    }
    .location-wrapper{
        height: 1px;
        font-size: 18px;
        position: relative;
        top: -2vh;
        left: -2vw;
    }
    .display-wrapper{
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        margin-bottom: 10px;
    }
    .stat-wrapper{
        display: flex;
        justify-content: space-between;
        height: 52px;
    }
}

@keyframes flicker {
    0% { opacity: 0.15795 }
    5% { opacity: 0.31511 }
    10% { opacity: 0.94554 }
    15% { opacity: 0.2469 }
    20% { opacity: 0.62031 }
    25% { opacity: 0.0293 }
    30% { opacity: 0.00899 }
    35% { opacity: 0.5344 }
    40% { opacity: 0.12778 }
    45% { opacity: 0.52042 }
    50% { opacity: 0.3823 }
    55% { opacity: 0.2198 }
    60% { opacity: 0.9383 }
    65% { opacity: 0.86615 }
    70% { opacity: 0.68695 }
    75% { opacity: 0.55749 }
    80% { opacity: 0.96984 }
    85% { opacity: 0.0361 }
    90% { opacity: 0.24467 }
    95% { opacity: 0.08351 }
    100% { opacity: 0.54813 }
}

::-webkit-scrollbar {
    display: none;
}


.overlay {
	height: 1px;
	position: absolute;
	top: 0;
	left: 0;
	width: 1px;
}
	
.overlay:after {
	animation: flicker 0.30s infinite;
	background: rgba(16, 16, 16, 0.2);
	content: "";
	display: block;
	pointer-events: none;
	position: fixed;
		top: 0;
		right: 0;
		bottom: 0;
		left: 0;
	z-index: 2;
}


@media screen and (max-width: 660px) {
    .content-wrapper{
        display: grid;
        grid-template-columns: 1fr;
        grid-template-rows: 1fr 1fr 12fr 2fr 3fr 3fr;    
    
        width: 96vw;
        height: 97vh;
    
        // margin: 1vh 1vw;
        margin: 1vh 1vw 1vh 2vw;
        padding: 2vh 3vw;
        box-sizing: border-box;
        border: solid $green3 5px;
        border-radius: 1px;

        .stat-wrapper{
            height: 72px;
        }
        .title-wrapper {
            font-size: 20px;
            display: flex;
            .title {
                margin: 5px auto;
                position: relative;
                top: -2vh;
            }
        
        }
        .location-wrapper{
            height: 1px;
            font-size: 18px;
            position: relative;
            top: 0px;
            left: 0px;
        }
    }
}